.space__home {
  min-height: 100vh;
  width: 100%;
  background: url("../../Assets/home/background-home-mobile.jpg") no-repeat
    center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

  display: flex;
  font-family: var(--font-main);
  text-align: center;
}

.space__home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 7rem;
}

.space__home-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.space__home-content h5 {
  font-family: var(--font-secondary);
  color: var(--color-purple);
  font-size: 16px;
  letter-spacing: 2.7px;
  line-height: 19px;
  font-weight: normal;
  margin-bottom: 1rem;
}

.space__home-content h1 {
  color: var(--color-white);
  font-size: 80px;
  line-height: 100px;
  margin-bottom: 1rem;
  font-weight: normal;
}

.space__home-content p {
  font-family: var(--font-secondary);
  font-size: 15px;
  line-height: 25px;
  color: var(--color-purple);
  width: 327px;
  margin-bottom: 5rem;
  width: 46ch;
}

.space__home-explore {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  width: 100%;
  height: 100%;
}

.space__home-explore-button {
  height: 150px;
  width: 150px;
  background: var(--color-white);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 20px;
  line-height: 23px;
  letter-spacing: 1.25px;
  color: var(--color-black);
  cursor: pointer;
}

.space__home-explore-button:hover {
  box-shadow: 0px 0px 0px 40px rgba(255, 255, 255, 0.1);
}

@media screen and (min-width: 500px) {
  .space__home {
    background: url("../../Assets/home/background-home-tablet.jpg") no-repeat
      center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }
}

@media screen and (min-width: 768px) {
  .space__home-container {
    margin-top: 12.625rem;
    display: flex;
  }

  .space__home-content h5 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    margin-bottom: 1.5rem;
  }

  .space__home-content h1 {
    font-size: 150px;
    line-height: 150px;
    margin-bottom: 1.5rem;
  }

  .space__home-content p {
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    width: 50ch;
    margin-bottom: 9.75rem;
  }
}

@media screen and (min-width: 1080px) {
  .space__home {
    background: url("../../Assets/home/background-home-desktop.jpg") no-repeat
      center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }
}

@media screen and (min-width: 1080px) {
  .space__home-container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 10.3rem;
  }

  .space__home-content {
    justify-content: flex-end;
    align-items: flex-start;
    width: fit-content;
  }

  .space__home-content h5 {
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    text-align: left;
    margin-bottom: 1.5rem;
  }

  .space__home-content h1 {
    font-size: 150px;
    line-height: 172px;
    margin-bottom: 1.5rem;
  }

  .space__home-content p {
    font-size: 18px;
    line-height: 32px;
    text-align: left;
    margin-bottom: 8.2rem;
  }

  .space__home-explore {
    align-items: flex-end;
    justify-content: flex-start;
    width: fit-content;
  }

  .space__home-explore-button {
    height: 274px;
    width: 274px;
    margin-bottom: 8.2rem;

    font-size: 32px;
    line-height: 37px;
    letter-spacing: 2px;
  }

  .space__home-explore-button:hover {
    box-shadow: 0px 0px 0px 60px rgba(255, 255, 255, 0.1);
  }
}
