.technology-main {
  min-height: 100vh;
  width: 100%;
  background: url("/src/Assets/technology/background-technology-mobile.jpg")
    no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

  color: white;
  font-family: var(--font-main);

  display: flex;
  flex-direction: column;
}

.technology-header {
  margin-bottom: 8.6rem;
  font-family: var(--font-secondary);
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;

  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 5.5rem;
  margin-bottom: 2rem;
}

.technology-header p {
  opacity: 0.25;
  font-weight: bold;
}

.technology-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.technology-image {
  width: 100%;
  height: 170px;
  margin-right: 0;
  margin-bottom: 2.125rem;
  display: flex;
  justify-content: center;
}

.technology-image img {
  width: 100%;
  height: 170px;
}

.technology-content {
  display: flex;
  flex-direction: column;
}

.technology-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.625rem;
}

.technology-nav-item {
  background: transparent;
  border: solid 1px rgba(255, 255, 255, 0.25);
  border-radius: 100%;
  height: 40px;
  width: 40px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 1px;
  color: var(--color-white);
  cursor: pointer;
}

.technology-nav-item:hover {
  border: solid 1px white;
}

.selected-tech {
  background-color: white;
  color: var(--color-black);
}

.technology-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.technology-subheader {
  font-family: var(--font-secondary);
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2.3625px;
  color: var(--color-purple);
  text-align: center;
  margin-bottom: 9px;
}

.technology-title {
  font-family: var(--font-main);
  font-size: 24px;
  line-height: 28px;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 1rem;
}

.technology-description {
  font-family: var(--font-secondary);
  font-weight: normal;
  font-size: 15px;
  color: var(--color-purple);
  line-height: 25px;
  text-align: center;
  margin-bottom: 3.5rem;
  width: 48ch;
}

.launchVehicle {
  content: url("/src/Assets/technology/image-launch-vehicle-landscape.jpg");
}

.spacePort {
  content: url("/src/Assets/technology/image-spaceport-landscape.jpg");
}

.spaceCapsule {
  content: url("/src/Assets/technology/image-space-capsule-landscape.jpg");
}

@media screen and (min-width: 550px) {
  .technology-main {
    background: url("/src/Assets/technology/background-technology-tablet.jpg")
      no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }

  .technology-image {
    height: 200px;
  }

  .technology-image img {
    height: 200px;
  }
}

@media screen and (min-width: 768px) {
  .technology-header {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    gap: 19px;

    justify-content: flex-start;
    margin-top: 8.5rem;
    margin-left: 2.5rem;
    margin-bottom: 3.75rem;
  }

  .technology-image {
    height: 310px;
    margin-bottom: 3.5rem;
  }

  .technology-image img {
    height: 310px;
    width: 768px;
  }

  .technology-nav-item {
    height: 60px;
    width: 60px;

    font-size: 24px;
    line-height: 28px;
    letter-spacing: 1.5px;
  }

  .technology-subheader {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .technology-title {
    font-size: 40px;
    line-height: 46px;
  }

  .technology-description {
    font-size: 16px;
    line-height: 28px;
    width: 48ch;
  }
}

@media screen and (min-width: 1080px) {
  .technology-main {
    background: url("/src/Assets/technology/background-technology-desktop.jpg")
      no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }
}

@media screen and (min-width: 1440px) {
  .launchVehicle {
    content: url("/src/Assets/technology/image-launch-vehicle-portrait.jpg");
  }

  .spacePort {
    content: url("/src/Assets/technology/image-spaceport-portrait.jpg");
  }

  .spaceCapsule {
    content: url("/src/Assets/technology/image-space-capsule-portrait.jpg");
  }

  .technology-image img {
    height: 527px;
    width: 515px;
  }

  .technology-header {
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    gap: 1.75rem;

    margin: 13.25rem 0 0rem 10.5rem;
  }

  .technology-container {
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: space-between;
  }

  .technology-image {
    width: 515px;
    height: 527px;
    margin: 0;
  }

  .technology-content {
    flex-direction: row;
    margin-left: 10.3rem;
    height: 305px;
    margin-top: 6rem;
  }

  .technology-nav {
    flex-direction: column;
    margin-right: 5.5rem;
  }

  .technology-nav-item {
    height: 80px;
    width: 80px;

    font-size: 32px;
    line-height: 37px;
    text-align: center;
    letter-spacing: 2px;
  }

  .technology-info {
    justify-content: center;
    align-items: flex-start;
  }

  .technology-subheader {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .technology-title {
    height: 64px;
    font-size: 56px;
    line-height: 64px;
    text-align: left;
    width: 100%;
    margin-bottom: 17px;
  }

  .technology-description {
    font-size: 18px;
    line-height: 32px;
    text-align: left;
    width: 43.5ch;
    height: 192px;
    margin: 0;
  }
}
